"use client";

import React, { useEffect, useState } from "react";
import { useRouter, useSearchParams } from "next/navigation";
import { ArrowLeft } from "lucide-react";
import { toast } from "sonner";
import { Button } from "@/components/ui/button";
import { RichTextEditor } from "../components/RichTextEditor";
import axios from "axios";
import { getToken } from "@/lib/auth";

export default function ImageUploadPage() {
  const router = useRouter();
  const searchParams = useSearchParams();
  const [post, setPost] = useState({
    title: "",
    content: "",
    subtitle: "",
    category_id: "",
    is_published: false,
    image: "",
  });

  useEffect(() => {
    // Load post data from URL params if in edit mode
    const editData = searchParams.get("edit");
    if (editData) {
      try {
        const parsedData = JSON.parse(decodeURIComponent(editData));
        setPost((prev) => ({
          ...prev,
          ...parsedData,
        }));
      } catch (e) {
        console.error("Error parsing edit data:", e);
        toast.error("Error loading post data");
      }
    }
  }, [searchParams]);

  const handleImageUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      // In a real app, you would upload the file to a server here
      const reader = new FileReader();
      reader.onloadend = () => {
        setPost((prev) => ({
          ...prev,
          image: reader.result as string,
        }));
      };
      reader.readAsDataURL(file);
    }
  };

  const handlePublish = async () => {
    try {
      console.log("Starting publish process...");
      const postData = {
        ...post,
        is_published: true,
        published_at: new Date().toISOString(),
      };
      
      console.log("Post data to be published:", JSON.stringify(postData, null, 2));

      // Get the authentication token
      const token = getToken();
      
      // Make the API call with Axios
      const response = await axios.post(
        "/api/posts",
        postData,
        {
          headers: {
            'Content-Type': 'application/json',
            'Authorization': token ? `Bearer ${token}` : ''
          }
        }
      );

      console.log("API Response:", response.data);
      console.log("Post published successfully!");
      
      toast.success("Post published successfully!");
      router.push("/dashboard");
    } catch (error) {
      console.error("Error publishing post:", error);
      
      if (axios.isAxiosError(error)) {
        console.error("Error details:", {
          status: error.response?.status,
          data: error.response?.data,
          headers: error.response?.headers
        });
        
        const errorMessage = error.response?.data?.message || "Failed to publish post. Please try again.";
        toast.error(errorMessage);
      } else {
        toast.error("An unexpected error occurred. Please try again.");
      }
    }
  };

  return (
    <div className="min-h-screen bg-gray-50">
      <header className="bg-white border-b border-gray-200 sticky top-0 z-10">
        <div className="max-w-6xl mx-auto px-6 py-4 flex items-center justify-between">
          <Button
            variant="ghost"
            onClick={() => router.back()}
            className="flex items-center gap-2"
          >
            <ArrowLeft className="w-4 h-4" />
            <span>Back</span>
          </Button>

          <div className="flex items-center gap-4">
            <Button
              variant="outline"
              onClick={() => {
                // Save as draft
                toast.success("Draft saved");
              }}
            >
              Save Draft
            </Button>
            <Button onClick={handlePublish}>Publish</Button>
          </div>
        </div>
      </header>

      <main className="max-w-4xl mx-auto px-6 py-8">
        <h1 className="text-3xl font-bold mb-8">Upload Featured Image</h1>

        <div className="bg-white rounded-lg border border-gray-200 p-6">
          <div className="mb-6">
            <h2 className="text-xl font-semibold mb-2">Featured Image</h2>
            <p className="text-gray-600 mb-4">
              Add a featured image to make your post more engaging.
            </p>

            <div className="mt-4">
              {post.image ? (
                <div className="relative group">
                  <img
                    src={post.image}
                    alt="Preview"
                    className="w-full h-64 object-cover rounded-lg"
                  />
                  <button
                    onClick={() => setPost((prev) => ({ ...prev, image: "" }))}
                    className="absolute top-2 right-2 bg-red-500 text-white p-1.5 rounded-full opacity-0 group-hover:opacity-100 transition-opacity"
                    aria-label="Remove image"
                  >
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="16"
                      height="16"
                      viewBox="0 0 24 24"
                      fill="none"
                      stroke="currentColor"
                      strokeWidth="2"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                    >
                      <line x1="18" y1="6" x2="6" y2="18"></line>
                      <line x1="6" y1="6" x2="18" y2="18"></line>
                    </svg>
                  </button>
                </div>
              ) : (
                <div className="border-2 border-dashed border-gray-300 rounded-lg p-8 text-center">
                  <div className="flex flex-col items-center justify-center">
                    <svg
                      className="w-12 h-12 text-gray-400 mb-3"
                      fill="none"
                      stroke="currentColor"
                      viewBox="0 0 24 24"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        strokeWidth="2"
                        d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14m-6-6h.01M6 20h12a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v12a2 2 0 002 2z"
                      ></path>
                    </svg>
                    <p className="text-sm text-gray-600 mb-2">
                      Drag and drop your image here, or click to select
                    </p>
                    <label className="cursor-pointer bg-purple-600 text-white px-4 py-2 rounded-lg hover:bg-purple-700 transition-colors">
                      Choose Image
                      <input
                        type="file"
                        accept="image/*"
                        className="hidden"
                        onChange={handleImageUpload}
                      />
                    </label>
                  </div>
                </div>
              )}
            </div>
          </div>

          <div className="mt-8">
            <h2 className="text-xl font-semibold mb-4">Post Preview</h2>
            <div className="bg-white border border-gray-200 rounded-lg p-6">
              <h1 className="text-3xl font-bold mb-4">
                {post.title || "Your Post Title"}
              </h1>
              {post.subtitle && (
                <p className="text-xl text-gray-600 mb-6">{post.subtitle}</p>
              )}
              {post.image && (
                <img
                  src={post.image}
                  alt=""
                  className="w-full h-64 object-cover rounded-lg mb-6"
                />
              )}
              <RichTextEditor
                content={
                  post.content || "<p>Your post content will appear here...</p>"
                }
                onChange={(content: string) =>
                  setPost((prev) => ({ ...prev, content }))
                }
              />
            </div>
          </div>
        </div>
      </main>
    </div>
  );
}
